@import 'global';
.container{
margin:0;
padding:0;
width:100vw;
height:100vh;
display:flex;
align-items:center;
justify-content: center;
background-image:url("../public/loginback.png");
background-repeat: no-repeat;
background-size: cover;
background-position: center center;

position:relative;
Image{
    position:relative;
    left:0 !important;
    
}
.loginContainer{
position:relative;
display:flex;
flex-direction:column;
justify-content: center;
width:45vw;
height:70vh;
border-radius:20px;
border: 1px solid #F27400;
background-color:white;
opacity:.9;
.inputContainer{
    top:0;
    display:flex;
    flex-direction: column;
    justify-content:center;
    width:100%;
    height:60%;

input{
    width:50%;
    margin:.5rem auto;
    height:3rem;
    padding:auto 1rem;
    &:hover{
        border: 1px solid black;
    }
}
h6{
    width:50%;
    margin:.1rem auto;
    font-family:$f-primary; 
}

}
}
.submitContainer{
    margin:.5rem auto;
    button{
        height:2rem;
        width:8rem;
        padding:auto;
        border-radius:1.5rem;
        font-family:$f-primary;
        color:black;
        background-color:$btn-color;
        border:none;
    &:hover{
        border:1px solid black;

        }

    }
}
}